
import AddCartProduct from '../components/product/AddCartProduct.vue';

export default {
  components: { AddCartProduct },
  props: ['id'],
  data() {
    return {
      isLoading: false,
      error: '',
      product: {},
      cacheImage: '',
      productQuantity: 1,
      addCartMessage: '',
    };
  },
  computed: {},
  methods: {
    async getProductDetail() {
      try {
        this.isLoading = true;
        await this.$store.dispatch('product/getProductDetails', this.id);
        this.product = await this.$store.getters['product/productData'];

        this.cacheImage = this.product.imageUrl;
      } catch (err) {
        this.error = err;
      } finally {
        this.isLoading = false;
      }
    },
    closeError() {
      this.error = '';
    },
    activeImg(img) {
      if (this.cacheImage === img) {
        return 'activeImg';
      }
      return '';
    },
    async addCart() {
      try {
        this.isLoading = true;

        const data = {
          product_id: this.id,
          qty: this.productQuantity,
        };
        await this.$store.dispatch('cart/addCart', data);
        this.addCartMessage = this.$store.getters['cart/addCart'];
      } catch (err) {
        this.error = err;
      } finally {
        this.isLoading = false;
      }
    },
    closeAddCartMessage() {
      this.addCartMessage = '';
    },
  },
  mounted() {
    this.getProductDetail();
  },
};
